import React from "react"
import SEO from "../components/seo"
import Logo from "../components/logo"
import Nav from "../components/navigation"
import RelaxImage from "../images/relax-image.jpg"
import PageInfo from "../components/pageinfo"
import { RELAX_INFO } from "../components/siteinfo"
import GiantHeader from "../components/giantheader"
import 'aos/dist/aos.css';

import { LOGO_COLOR_LIGHT, MAIN_TEXT_COLOR_LIGHT } from "../components/siteconfig"

class Relax extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isActive: true
    }
  }
  componentDidMount() {
    const AOS = require('aos');
    this.aos = AOS
    this.aos.init()
  }

  componentDidUpdate() {
    this.aos.refresh()
  }
  render() {
    return (

      <div style={{ display: `flex`, background: `url(${RelaxImage})`, backgroundSize: `cover`, backgroundRepeat: `no-repeat`, backgroundAttachment: `fixed`, backgroundPosition: `center`, width: `100%`, height: `2000px` }}>
        <SEO title="Relax - 1240 Greene St" />
        <div css={{ width: '50%' }} >
          <Logo title="1240 Greene St" color={LOGO_COLOR_LIGHT} />
          <div data-aos="fade-right" data-aos-delay="300" data-aos-easing="ease-in-sine" data-aos-duration="500">
            <PageInfo header={RELAX_INFO.header} decscription={RELAX_INFO.description} color={MAIN_TEXT_COLOR_LIGHT} />
          </div>
        </div>

        <div css={{ zIndex: `100`, marginLeft: `auto`, justifyContent: `flex-end` }}>
          <Nav isActive={this.state.isActive} />
          <div css={{
            position: 'fixed',
            bottom: '-117px',
            left: '-71%',
            transform: 'scale(.4)',
            '@media(min-width:700px)': {
              bottom: '-68px',
              transform: 'scale(1)',
              left: '39%'
            }
          }}>
            <div data-aos="fade-left" data-aos-delay="350" data-aos-duration="500" data-aos-easing="ease-in-sine">
              <GiantHeader text="Relax" color="#fff" opacity=".4" stroke="1px #000" />
            </div>
          </div>
        </div>


      </div>
    )
  }
}



export default Relax
